import React from 'react';
import { Breadcrumbs, Typography } from '@mui/material';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';

const crumbs = ["Indicators", "Budget", "Network (optional)", "Calibration", "Simulation"];

const Breadcrumb = ({ text, selected }) => (
  <Typography variant={selected ? 'breadcrumbSelected' : 'breadcrumb'}>
    {text}
  </Typography>
)

const CustomBreadcrumbs = ({ selected }) => {
  const breadcrumbs = crumbs.map((crumb, index) => <Breadcrumb key={index} text={crumb} selected={selected === index} />);

  return (
    <Breadcrumbs
      separator={<NavigateNextIcon fontSize="small"/>}
      aria-label="breadcrumb"
    >
      {breadcrumbs}
    </Breadcrumbs>
  );
}

export { CustomBreadcrumbs };
;